*, :before, :after {
  box-sizing: border-box;
}

html {
  tab-size: 4;
  -webkit-text-size-adjust: 100%;
  line-height: 1.15;
}

body {
  margin: 0;
  font-family: system-ui, AppleSystem, BlinkMacSystemFont, Segoe UI, Roboto, Noto Sans, Ubuntu, Cantarell, Helvetica Neue, AppleSystem, BlinkMacSystemFont, Segoe UI, Roboto, Noto Sans, Ubuntu, Cantarell, Helvetica Neue, -apple-system, Segoe UI, Roboto, Helvetica, Arial, sans-serif, Apple Color Emoji, Segoe UI Emoji;
}

hr {
  height: 0;
  color: inherit;
}

abbr[title] {
  -webkit-text-decoration: underline dotted;
  text-decoration: underline dotted;
}

b, strong {
  font-weight: bolder;
}

code, kbd, samp, pre {
  font-family: ui-monospace, SFMono-Regular, Consolas, Liberation Mono, Menlo, monospace;
  font-size: 1em;
}

small {
  font-size: 80%;
}

sub, sup {
  vertical-align: baseline;
  font-size: 75%;
  line-height: 0;
  position: relative;
}

sub {
  bottom: -.25em;
}

sup {
  top: -.5em;
}

table {
  text-indent: 0;
  border-color: inherit;
}

button, input, optgroup, select, textarea {
  margin: 0;
  font-family: inherit;
  font-size: 100%;
  line-height: 1.15;
}

button, select {
  text-transform: none;
}

button, [type="button"], [type="reset"], [type="submit"] {
  -webkit-appearance: button;
}

::-moz-focus-inner {
  border-style: none;
  padding: 0;
}

:-moz-focusring {
  outline: 1px dotted ButtonText;
}

:-moz-ui-invalid {
  box-shadow: none;
}

legend {
  padding: 0;
}

progress {
  vertical-align: baseline;
}

::-webkit-inner-spin-button, ::-webkit-outer-spin-button {
  height: auto;
}

[type="search"] {
  -webkit-appearance: textfield;
  outline-offset: -2px;
}

::-webkit-search-decoration {
  -webkit-appearance: none;
}

::-webkit-file-upload-button {
  -webkit-appearance: button;
  font: inherit;
}

summary {
  display: list-item;
}

.products-section {
  width: 100%;
  background: var(--white-text-color);
}

.titles-products {
  display: block;
}

.products-div {
  width: 100%;
  background: var(--white-text-color);
  justify-content: center;
  display: block;
}

.products-paragrapgh {
  color: var(--cream-text-color);
  text-align: center;
  font-family: var(--font-main);
  text-transform: uppercase;
  padding-top: 60px;
  font-size: 18px;
  font-weight: 400;
  transition: transform .25s ease-in-out;
}

.products-paragrapgh:hover {
  animation: springy-text 2s ease-in-out forwards;
  transform: scale(1.3);
}

.products-title {
  height: 36px;
  color: var(--red-text-color);
  text-align: center;
  letter-spacing: 1.36px;
  text-transform: uppercase;
  font-family: var(--font-main);
  margin-top: 15px;
  margin-bottom: 40px;
  font-size: 34px;
  font-weight: 400;
  transition: transform .25s ease-in-out;
}

.products-title:hover {
  cursor: default;
  animation: springy-text 2s ease-in-out forwards;
  transform: scale(1.3);
}

.products-card {
  justify-content: center;
  padding-top: 8px;
  display: block;
  position: relative;
}

.product1 {
  width: 280px;
  height: 489px;
  background: var(--products-first-color);
  border-radius: 24px;
  margin: 148px auto 60px;
  transition: transform .25s ease-in-out;
  position: relative;
}

.product1:hover {
  transform: scale(1.03);
}

.product-overlay {
  width: 100%;
  height: 100%;
  z-index: 0;
  opacity: 0;
  background-color: rgba(91, 92, 94, .15);
  border-radius: 24px;
  transition: opacity 50ms cubic-bezier(.4, 0, .2, 1), box-shadow 50ms cubic-bezier(.4, 0, .2, 1);
  position: absolute;
  top: 0;
  left: 0;
}

.product1:hover .product-overlay {
  cursor: pointer;
  opacity: 1;
  box-shadow: 0 5px 15px rgba(144, 35, 62, .63);
}

.product1:hover .icecream-paragraph {
  visibility: hidden;
}

.product1:hover .icecream-title {
  z-index: 1;
  font-size: 29px;
  transition: all .35s cubic-bezier(.4, 0, .2, 1);
  position: absolute;
}

.product1:hover .arrow-right-product {
  cursor: pointer;
  z-index: 1;
  width: 49px;
  height: 25px;
  fill: var(--white-text-color);
  border-radius: 35%;
  transition: all .35s cubic-bezier(.4, 0, .2, 1);
  transform: rotate(-90deg);
}

.product1:hover .icons {
  cursor: pointer;
  z-index: 1;
  width: 50px;
  height: 50px;
  background-color: var(--red-text-color);
  border-radius: 35%;
  transition: all .35s cubic-bezier(.4, 0, .2, 1);
  transform: rotate(0deg);
}

.product1:hover .dots-product {
  z-index: 1;
}

.icons {
